document.addEventListener('DOMContentLoaded', (event) => {
  const accordionItems = document.querySelectorAll('.accordion__content-item');

  accordionItems.forEach(item => {
      item.addEventListener('click', function() {
          this.classList.toggle('isActive');

          const moreIcon = this.querySelector('.accordion__more');

          // Check if the accordion is active to determine whether we should show the plus or minus sign.
          if (this.classList.contains('isActive')) {
              // change more icon to minus
              moreIcon.classList.remove('plus');
              moreIcon.classList.add('minus');
          } else {
              // change more icon to plus
              moreIcon.classList.remove('minus');
              moreIcon.classList.add('plus');
          }
      });
  });
});
