document.addEventListener('DOMContentLoaded', function () {
  const burgerMenu = document.getElementById('burger-button');
  const menu = document.getElementById('menu');
  const close = document.querySelector('.close');
  const navItems = menu.querySelectorAll('li');
  const langWrapper = document.querySelector('.burger-menu__lang-wrapper');
  const socials = document.querySelector('.burger-menu__socials');

  function animateElements(elements, animationName) {
      elements.forEach((element, index) => {
          element.style.animationDelay = `${index * 0.1 + 0}s`; // Increase the delay by 0.1s
          element.classList.remove(animationName === 'slideIn' ? 'slideOut' : 'slideIn');
          element.classList.add(animationName);
      });
  }

  function toggleScroll() {
      document.body.classList.toggle('scroll-disabled');
  }

  burgerMenu.addEventListener('click', () => {
      burgerMenu.classList.toggle('open');

      if (menu.classList.contains('open')) {
          menu.classList.remove('open');
          menu.classList.add('close');
          animateElements([...navItems].reverse(), 'slideOutTop'); // Use new 'slideOutTop' animation
          langWrapper.classList.remove('slideIn');
          langWrapper.classList.add('slideOut');
          socials.classList.remove('slideIn');
          socials.classList.add('slideOut');
          close.style.display = 'none'; // Hide the close span when menu is closed
          setTimeout(toggleScroll, 500); // Call toggleScroll after 0.5 seconds (500 milliseconds)
      } else {
          menu.classList.remove('close');
          menu.classList.add('open');
          animateElements(navItems, 'slideIn');
          langWrapper.classList.remove('slideOut');
          langWrapper.classList.add('slideIn');
          socials.classList.remove('slideOut');
          socials.classList.add('slideIn');
          close.style.display = 'flex'; // Display the close span when menu is open
          toggleScroll(); // Disable scrolling
      }


  });
});
