// JavaScript code
const imageContainers = document.querySelectorAll('.services__image-container');

imageContainers.forEach((container) => {
  container.addEventListener('mouseenter', () => {
    container.style.opacity = '1';
    container.querySelector('.services__image').style.filter = 'grayscale(0%)';
  });

  container.addEventListener('mouseleave', () => {
    container.style.opacity = '0.5';
    container.querySelector('.services__image').style.filter = 'grayscale(100%)';
  });
});
