    autoplay: true,
    speed: 5000,  // время анимации скольжения (в миллисекундах)
    autoplaySpeed: 0,  // время задержки между автоматическими переходами (в миллисекундах)
    cssEase: 'linear',  // тип анимации (линейная анимация для непрерывного скольжения)
