if (location.pathname.includes('/service.html')) {
  // Создаем экземпляр Flickity для слайдера
  var elem = document.querySelector('.testimonials__slider');
  var flkty = new Flickity(elem, {
    cellAlign: 'left',
    contain: true,
    prevNextButtons: false,
    pageDots: false,
    wrapAround: true,
  });

  // Находим кнопки влево и вправо
  var buttonLeft = document.querySelector('.round-button--left');
  var buttonRight = document.querySelector('.round-button--right');

  // Обработчик для кнопки влево
  buttonLeft.addEventListener('click', function () {
    flkty.previous();
  });

  // Обработчик для кнопки вправо
  buttonRight.addEventListener('click', function () {
    flkty.next();
  });

  // Проверка на сенсорное устройство
  var isTouchDevice = 'ontouchstart' in window || navigator.msMaxTouchPoints;

  if (isTouchDevice) {
    // Создаем отдельный экземпляр Flickity для сенсорных устройств
    var mobileElem = document.querySelector('.testimonials__slider');
    var mobileFlkty = new Flickity(mobileElem, {
      cellAlign: 'left',
      contain: true,
      prevNextButtons: false,
      pageDots: false,
      wrapAround: true,
      groupCells: false,
    });

    // Находим кнопки влево и вправо для сенсорных устройств
    var mobileButtonLeft = document.querySelector('.round-button--left--mobile');
    var mobileButtonRight = document.querySelector('.round-button--right--mobile');

    // Обработчик для кнопки влево на сенсорных устройствах
    mobileButtonLeft.addEventListener('click', function () {
      mobileFlkty.previous();
    });

    // Обработчик для кнопки вправо на сенсорных устройствах
    mobileButtonRight.addEventListener('click', function () {
      mobileFlkty.next();
    });
  }
}

