if (location.pathname.includes('/index.html')) {
  const logo = document.querySelector('.logo');
  const burgerLines = document.querySelectorAll('.burger-menu__line');

  // Select the header element
  const header = document.querySelector('.header');

  // Variables to track the previous scroll position and scroll direction
  let prevScrollPos = window.pageYOffset;
  let scrollDirection = '';

  // Function to handle the scroll event
  const handleScroll = () => {
    // Get the current scroll position
    const currentScrollPos = window.pageYOffset;

    // Determine the scroll direction
    if (currentScrollPos > prevScrollPos) {
      scrollDirection = 'down';
    } else {
      scrollDirection = 'up';
    }

    // If window width is greater than 999px, make header visible only when scrolling up
    if (window.innerWidth > 999) {
      if (scrollDirection === 'up' && currentScrollPos > header.offsetHeight - 160) {
        header.classList.add('scrolled');
        header.style.opacity = '1';
      } else {
        header.classList.remove('scrolled');
      }
    }
    // If window width is less than or equal to 999px, make header always visible
    else {
      header.classList.add('scrolled');
      header.style.opacity = '1';
    }

    // Fade in the header when scrolling to the top
    if (currentScrollPos <= 0) {
      const opacity = 1 - currentScrollPos / header.offsetHeight;
      header.style.opacity = opacity;
    }

    // Change logo color to black when passing specific sections, otherwise white
    const revertPosition = document.querySelector('.text-image.isRevert').offsetTop;
    const contactPosition = document.querySelector('.contact').offsetTop;
    const contactHeight = document.querySelector('.contact').offsetHeight; // Высота секции "Contact"
    const textImageHeight = document.querySelector('.text-image.isRevert').offsetHeight; // Высота секции "text-image.isRevert"

    if ((currentScrollPos + header.offsetHeight >= revertPosition && currentScrollPos <= revertPosition + textImageHeight) || (currentScrollPos + header.offsetHeight >= contactPosition && currentScrollPos <= contactPosition + contactHeight)) {
      logo.classList.add('black');
    } else {
      logo.classList.remove('black');
    }

    // Change burger lines color to black when passing the "Contact" section, otherwise white
    if (currentScrollPos + header.offsetHeight >= contactPosition && currentScrollPos <= contactPosition + contactHeight) {
      burgerLines.forEach(line => line.classList.add('black'));
    } else {
      burgerLines.forEach(line => line.classList.remove('black'));
    }


    // Update the previous scroll position
    prevScrollPos = currentScrollPos;
  };

  // Add event listener to the scroll event
  window.addEventListener('scroll', handleScroll);


}


